import react from "react";

const Todolist = (props) => {
  return (
    <>
      <div className="todo_style">
        <i
          className="fas fa-trash-alt"
          onClick={() => {
            props.onSelect(props.id);
          }}
        />
        <li>{props.text}</li>
      </div>
    </>
  );
};

export default Todolist;
